@import "https://fonts.googleapis.com/css?family=Cherry+Swash";
*, :before, :after {
  box-sizing: border-box;
}

html, body, div, span, object, iframe, figure, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, code, em, img, small, strike, strong, sub, sup, tt, b, u, i, ol, ul, li, fieldset, form, label, table, caption, tbody, tfoot, thead, tr, th, td, main, canvas, embed, footer, header, nav, section, video {
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  text-rendering: optimizelegibility;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: none;
  -moz-text-size-adjust: none;
  text-size-adjust: none;
  border: 0;
  margin: 0;
  padding: 0;
}

footer, header, nav, section, main {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input {
  -webkit-appearance: none;
  border-radius: 0;
}

a {
  text-decoration: none;
}

body {
  background-color: #34495e;
  padding-top: 10vh;
}

header {
  height: 10vh;
  width: 100%;
  background-color: #34495e;
  justify-content: space-between;
  align-items: center;
  padding-right: 5%;
  display: flex;
  position: fixed;
  top: 0;
}

.logo {
  height: 100%;
  width: 10%;
  justify-content: center;
  align-items: center;
  display: flex;
}

@media screen and (min-width: 767.5px) {
  .logo {
    width: 30%;
  }
}

.logo img {
  width: 20px;
}

@media screen and (min-width: 767.5px) {
  .logo img {
    width: 40px;
  }
}

nav {
  width: 590px;
  height: 50px;
  background-color: #4864819c;
  border-radius: 8px;
  margin: auto;
  font-size: 0;
  display: flex;
  position: relative;
}

nav a {
  z-index: 1;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  font-family: sans-serif;
  font-size: 15px;
  display: flex;
  position: relative;
}

nav .animation {
  height: 100%;
  z-index: 0;
  border-radius: 8px;
  transition: all .5s;
  position: absolute;
  top: 0;
}

a:first-child {
  width: 100px;
}

a:nth-child(2) {
  width: 110px;
}

a:nth-child(3) {
  width: 100px;
}

a:nth-child(4) {
  width: 160px;
}

a:nth-child(5) {
  width: 120px;
}

.home__nav .active__nav, a:first-child:hover ~ .animation {
  width: 100px;
  background-color: #1abc9c;
  left: 0;
}

.about__nav .active__nav, a:nth-child(2):hover ~ .animation {
  width: 110px;
  background-color: #e74c3c;
  left: 100px;
}

.blog__nav .active__nav, a:nth-child(3):hover ~ .animation {
  width: 100px;
  background-color: #3498db;
  left: 210px;
}

.portfolio__nav .active__nav, a:nth-child(4):hover ~ .animation {
  width: 160px;
  background-color: #9b59b6;
  left: 310px;
}

.contacts__nav .active__nav, a:nth-child(5):hover ~ .animation {
  width: 120px;
  background-color: #e67e22;
  left: 470px;
}

.home__nav > .active__nav {
  background-color: red;
}

